.signup-wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
}

.signup-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25%;
  min-width: 450px;
  height: auto;
  padding: 10px;
  background: rgb(220, 220, 220, 0.8);
  font-size: 17px;
  /* background: white; */
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 30); */
  border-radius: 3%;
}

.signup-container h2 {
  background-color: #f4511e;
  text-align: center;
  padding: 16px 32px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: white;
  margin: 20px auto 30px;
  border-radius: 7px;
  width: 80%;
}

.signup-container label,
.signup-container input {
  display: inline;
  margin: 10px 5px;
  border: none;
  border-radius: 4px;
}
.signup-container input[type="file"] {
  max-width: 270px;
}
.signup-container .inp-container {
  display: flex;
  justify-content: space-between;
  width: 80%;
  min-width: 280px;
  margin: 0 auto;
}

.img-input {
  padding-left: 50px;
}

.signup-container h5 {
  margin-top: 25px;
}

.user-radio {
  margin: 0px auto;
  display: block;
  width: 80%;
  min-width: 280px;
}

.user-radio label {
  font-size: 14px;
}

.signup-btn {
  display: block;
  width: 80%;
  margin: 0 auto 20px;
  text-align: center;
  font-size: x-large;
  border: none;
  cursor: pointer;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: #f4511e;
  color: white;
  padding: 16px 32px;
  font-size: 16px;
  opacity: 1;
  transition: 0.3s;
  margin-top: 37px;
  border-radius: 5px;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.signup-btn:hover {
  opacity: 1;
  background-color: black;
  opacity: 1;
  /* outline: none; */
  text-decoration: none;
}
@media screen and (max-width: 600px) {
  .signup-container {
    min-width: 300px;
    margin: 0;
  }
  .signup-container h2 {
    font-size: 1.5rem;
  }

  .signup-container input[type="text"],
  .signup-container input[type="email"],
  .signup-container input[type="password"] {
    width: 150px;
  }
}

